	})
	const getCountList=()=>{
		countTypeList.value.closeyongshuixinxiCountType = true
		countTypeList.value.hiddenyongshuixinxiCountType = true
		if(btnAuth('yongshuixinxi','首页总数')){
			getyongshuixinxiCount()
		}
		countTypeList.value.closeyongdianxinxiCountType = true
		countTypeList.value.hiddenyongdianxinxiCountType = true
		if(btnAuth('yongdianxinxi','首页总数')){
			getyongdianxinxiCount()
		}
	}
	const yongshuixinxiCount = ref(0)
	const getyongshuixinxiCount = () => {
		context?.$http({
			url:'yongshuixinxi/count',
			method: 'get'
		}).then(res=>{
			yongshuixinxiCount.value = res.data.data
		})
	}
	const yongdianxinxiCount = ref(0)
	const getyongdianxinxiCount = () => {
		context?.$http({
			url:'yongdianxinxi/count',
			method: 'get'
		}).then(res=>{
			yongdianxinxiCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closeyongshuixinxiChartType1: true,
		hiddenyongshuixinxiChartType1: true,
		closeyongshuixinxiChartType2: true,
		hiddenyongshuixinxiChartType2: true,
		closeyongdianxinxiChartType1: true,
		hiddenyongdianxinxiChartType1: true,
		closeyongdianxinxiChartType2: true,
		hiddenyongdianxinxiChartType2: true,
	})
	const getCardList = () => {
		if(btnAuth('yongshuixinxi','首页统计')){
			getyongshuixinxiChart1()
		}
		if(btnAuth('yongshuixinxi','首页统计')){
			getyongshuixinxiChart2()
		}
		if(btnAuth('yongdianxinxi','首页统计')){
			getyongdianxinxiChart1()
		}
		if(btnAuth('yongdianxinxi','首页统计')){
			getyongdianxinxiChart2()
		}
	}
	const getyongshuixinxiChart1 = () => {
		nextTick(()=>{
			var benyueyongshuiliangEchart1 = echarts.init(document.getElementById("yongshuixinxibenyueyongshuiliangEchart1"),'macarons');
			context?.$http({
				url: `yongshuixinxi/value/yonghuzhanghao/benyueyongshuiliang`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].yonghuzhanghao);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].yonghuzhanghao
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '用户用水统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				benyueyongshuiliangEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				benyueyongshuiliangEchart1.setOption(option);
				//根据窗口的大小变动图表
				benyueyongshuiliangEchart1.resize();
			})
		})
	}
	const getyongshuixinxiChart2 = () => {
